#tryagain {
  font-weight: 990;
  text-align: center;
  position: relative;
  cursor: pointer;
  grid-column: 5;
  grid-row: 2;
  width: 116px;
  height: 116px;
  border-radius: 65px;
  border: 4px solid green;
  font-family: Papyrus;
  color: white;
  background-color: rgb(255, 77, 136);
  font-size: 25px; 
  line-height: 0.8;

}
#dino {
  font-weight: 990;
  text-align: center;
  position: relative;
  cursor: pointer;
  grid-column: 5;
  grid-row: 1;
  width: 116px;
  height: 116px;
  border-radius: 65px;
  border: 4px solid yellow;
  font-family: Papyrus;
  color: white;
  background-color: rgb(64, 191, 64);
  font-size: 25px; 
  line-height: 0.8;
  margin-left: 20px;
}
#dino:hover, #tryagain:hover {
box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
transition: all 0.7s;
}
